.profile {
  position: relative;
  overflow: hidden;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.profile__mask {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  opacity: .35;
  background-color: black;
}

.profile__bg {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
}

.profile__content {
  position: relative;
  z-index: 2;
  max-width: 80%;
  color: #f5f5f5;
  text-align: center;
}

.profile__content__avatar {
  width: 160px;
  height: 160px;
  margin: 0 auto;
  background: #333;
  padding: 3px;
  border-radius: 50%;
}

.profile__content__avatar img {
  max-width: 100%;
  border-radius: 50%;
}

.profile__content__desc {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
  padding-right: 2em;
  padding-left: 2em;
  margin: 2em auto;
  max-width: 440px;
  font-family: sans-serif;
}

.profile__content__social-media {
  text-align: center;
}

.profile__content__social-media .icon {
  margin: 0 .3em;
}

video {
  min-width: 100%;
  bottom: 0;
  position: absolute;
  min-height: 100%;
}

.icon {
  width: 32px;

}


